export default function ClientView(props) {
    const dataProvider = useDataProvider();
    const [selected, setSelected] = React.useState({})
    const [filter, setFilter] = React.useState(false)
    const [rows, setRows] = React.useState([])
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState();
    const [gridRef, setGridRef] = React.useState(null)

    const [state, setState] = React.useState({});

    const [refresh, setRefresh] = React.useState(false)

    const onColumnVisibleChange = ({ column, visible }) => {

        let newstate = getSavedState()


        if (!visible) {
            let search = newstate.hidden.find(i => i == column.name)
            !search && newstate.hidden.push(column.name)
            saveState(newstate)
            setRefresh(!refresh)

        }
        else {

            let search = newstate.hidden.find(i => i == column.name)
            search && newstate.hidden.splice(newstate.hidden.indexOf(search), 1)
            saveState(newstate)
            let newCol = columns

            newCol.forEach(y => { if (y.name == column.name) { y.visible = true } })
            setColumns(newCol)
            setRefresh(!refresh)
        }

    }

    // const onBatchColumnResize = (batchColumnInfo, { reservedViewportWidth }) => {
    //   const colsMap = batchColumnInfo.reduce((acc, colInfo) => {
    //     const { column, width, flex } = colInfo
    //     acc[column.name] = { width, flex }
    //     return acc
    //   }, {})

    //   const nncolumns = columns.map(c => {
    //     return Object.assign({}, c, colsMap[c.name])
    //   })
    //   let newstate = getSavedState()
    //   newstate.columnWidth = nncolumns
    //   setState(newstate);
    //   saveState(newstate)

    // }

    const onColumnOrderChange = (columnOrder) => {
        let newstate = getSavedState()
        newstate.columnOrder = columnOrder
        setState(newstate);
        saveState(newstate)
    }

    const onSortInfoChange = (sortInfo) => {
        let newstate = getSavedState()
        newstate.sortInfo = sortInfo
        setState(newstate);
        saveState(newstate)
    }

    const cellDOMProps = (cellProps) => {
        return {
            onClick: () => {
                gridRef.current.startEdit({ columnId: cellProps.id, rowIndex: cellProps.rowIndex })
            }
        }
    }

    const onEditComplete = ({ value, columnId, rowIndex, data }) => {
        try {
            if (columnId === 'suburb') {


                let nrows = rows
                nrows[nrows.findIndex(i => i.id === data.id)].suburb = value
                setRows(nrows)
                dataProvider.update('clients', { id: data.id, data: { suburb: value } })


            }
            else if (columnId === 'instagram') {


                let nrows = rows
                nrows[nrows.findIndex(i => i.id === data.id)].instagram = value
                setRows(nrows)
                dataProvider.update('clients', { id: data.id, data: { instagram: value } })


            }
            else if (columnId === 'phone') {


                let nrows = rows
                nrows[nrows.findIndex(i => i.id === data.id)].phone = value
                setRows(nrows)
                dataProvider.update('clients', { id: data.id, data: { phone: value } })


            }
            else if (columnId === 'email') {


                let nrows = rows
                nrows[nrows.findIndex(i => i.id === data.id)].email = value
                setRows(nrows)
                dataProvider.update('clients', { id: data.id, data: { email: value } })


            }




        }
        catch { }

    }

    const filterTypes = Object.assign({}, ReactDataGrid.defaultProps.filterTypes, {
        dogs: {
            type: 'string',
            emptyValue: '',
            operators: [
                {
                    name: 'contains',
                    fn: ({ value, filterValue, data }) => {
                        debugger
                        return !filterValue ?
                            true :
                            value && value.map(i => { if (i.dog_name.toLowerCase().indexOf(filterValue.toLowerCase()) != -1) { return true } })[0];
                    }
                }],
        }
    });


    const defaultColumns = [
        {
            name: 'name', header: 'Name', defaultWidth: 200, editable: false
        },
        {
            name: 'email', header: 'E-mail', defaultWidth: 280, render: ({ value }) => {

                return (<a href={"mailto:" + value} >{value}</a>)
            }
            , cellDOMProps
        },
        {
            name: 'phone', header: 'Phone', defaultWidth: 150, cellDOMProps
        },
        {
            name: 'dog', header: 'Dog', defaultWidth: 200, render: ({ value, data }) => {
                try {
                    return <>
                        {value.map(i => {

                            return (<a href={'#dogs/' + i.id} ><span style={{ paddingRight: '5px' }}>{i.dog_name}</span></a>)

                        })}
                    </>
                }
                catch { return }


            }
            , editable: false
        },
        {
            name: 'instagram', header: 'Instagram', defaultWidth: 150, cellDOMProps
        },
        {
            name: 'suburb', header: 'Suburb', defaultWidth: 150, cellDOMProps
        },

    ];

    const STORE = {

        hidden: [],
        sortInfo: [],
        reservedViewportWidth: 0,
        columnOrder: defaultColumns.map(i => i.name)
    }

    const getSavedState = () => {
        return JSON.parse(localStorage.getItem(`state-${props.title}`))
    }

    const saveState = (state) => {
        localStorage.setItem(`state-${props.title}`, JSON.stringify(state))
    }

    const gridStyle = { minHeight: 550 };

    const defaultFilterValue = [


        { name: 'phone', operator: 'contains', type: 'string', value: '' },
        { name: 'email', operator: 'contains', type: 'string', value: '' },
        { name: 'name', operator: 'contains', type: 'string', value: '' },
        { name: 'dog', operator: 'contains', type: 'dogs', value: '' },
        { name: 'instagram', operator: 'contains', type: 'string', value: '' },
        { name: 'suburb', operator: 'contains', type: 'string', value: '' },

    ];


    const [columns, setColumns] = React.useState(defaultColumns)
    React.useEffect(() => {
        let savedState = localStorage.getItem(`state-${props.title}`) || {};
        if (Object.keys(savedState).length === 0) {
            setState(STORE)
            saveState(STORE)
        }
        else {
            let newstate = JSON.parse(savedState)
            if (newstate.hidden.length != 0) {
                let newCol = columns
                newstate.hidden.forEach(i => newCol.forEach(y => { if (y.name == i) { y.visible = false } }))

                setColumns(newCol)
            }
            // if (newstate.columnWidth){
            //   columns.forEach(i => {
            //    let storedColumn= newstate.columnWidth.find(y=> y.name==i.name)
            //     if (storedColumn.width){
            //       i.width = storedColumn.width
            //     }
            //   })

            // }
            setState(newstate)
        }

    }, [refresh])


    React.useEffect(() => {





        async function fetchFata() {
            let allDogs = await dataProvider.getList('dogs', { 'pagination': { "page": 1, "perPage": 1000000 } })
            let clientsWDogs = props.rows || []
            clientsWDogs.forEach(client => { client.dog = allDogs.data.filter(dog => dog.owner === client.id) })


            setRows(clientsWDogs)
        }
        fetchFata()


    }, [props.rows])


    const create = (event) => {
        let rows = this.state.rows
        let id = event.id
        let even = event.data()
        even.id = id
        rows.push(even)
    }

    const update = (event) => {
        let rows = this.state.rows
        debugger
        let id = event.id
        let index = this.state.rows.findIndex(i => i.id === id)
        rows[index] = event.data()
        rows[index].id = id

        this.setState({ rows: rows })
    }
    /*
        componentWillUnmount() {
    
          //  this.subs()
        }
    
    
    */




    return (
        <>
            <Title title={props.title} />
            <div>
                <Button
                    label="Filters"
                    onClick={() => setFilter(!filter)}>
                    <FilterListIcon />
                </Button>
                {Object.keys(selected).length === 1 && <EditButton
                    basePath='/clients' label='Edit' record={Object.values(selected)[0]}
                />}
                {Object.keys(selected).length !== 0 && <Button
                    label="Message Client">
                    <SendIcon />
                </Button>}
            </div>
            <ReactDataGrid
                idProperty="id"
                columns={columns}
                columnOrder={state.columnOrder}
                dataSource={rows}
                filterTypes={filterTypes}
                onReady={setGridRef}
                onEditComplete={onEditComplete}
                editable={true}
                style={gridStyle}
                onColumnOrderChange={onColumnOrderChange}
                onColumnVisibleChange={onColumnVisibleChange}
                defaultFilterValue={defaultFilterValue}
                onSelectionChange={select => setSelected(select.selected)}
                enableFiltering={filter}
                multiSelect
                checkboxColumn
            />
        </>
    )
}











const PostTitle = ({ record }) => {
    return <span>Client {record ? `"${record.name}"` : ''}</span>;
}

export const UserCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <TextInput source="name" label='Name' />
            <TextInput source="phone" label="Phone" />
            <TextInput source="email" label="Email" />
            <TextInput source="instagram" label="Instagram" />
        </SimpleForm>
    </Create>
);

export const UserEdit = props => (
    <Edit title={<PostTitle />} {...props} >
        <SimpleForm>
            <TextInput source="name" label='Name' />
            <TextInput source="phone" label="Phone" />
            <TextInput source="email" label="Email" />
            <TextInput source="instagram" label="Instagram" />
        </SimpleForm>
    </Edit>
);




